














import {Component, Prop, Vue} from "vue-property-decorator"
import {IStudentProfile, ITrainerProfile} from "@chaosinsight/yogipeople-portalclient"
import Portal from "@/managers/portal"
import ImageOptimize from "@/managers/imageOptimize"

@Component({ components: { } })
export default class MenuAside extends Vue {
	@Prop()
	public profile!: IStudentProfile | ITrainerProfile

	@Prop()
	public menuOpen!: boolean

	@Prop()
	public menuItems!: object[]

	/**
	 * Compress and Set Profile Picture
	 */

	private setPicture(event: Event): void {
		const input = event.target as HTMLInputElement

		if (input.files === null || input.files.length === 0)
			return

		const file = input.files[0]
		input.value = ""

		ImageOptimize.compress(file, 500, 500).then(image => {
			Portal.client.call.profile.setPicture(image)
			.response
			.then(profile => this.$emit("profileUpdate"))
		})
	}
}
